document.addEventListener('DOMContentLoaded', () => {
    startApp();
})

function startApp():void{
    createGallery();
    scrollnav();
    fixedNavbar();
}

function createGallery():void {
    const gallery = document.querySelector<HTMLUListElement>('.image-list');
    
    for (let i = 1; i <= 12; i++) {
        const image:HTMLElement = document.createElement('picture')
        image.innerHTML = `
            <source srcset="build/img/thumb/${i}.avif" type="image/avif">
            <source srcset="build/img/thumb/${i}.webp" type="image/webp">
            <img loading="lazy" width="300" height="200" srcset="build/img/thumb/${i}.jpg" type="image/jpg">
        `;
        image.onclick = function(){
            showImage(i)
        }
        gallery?.appendChild(image);
    }
    
}
function showImage(id:number):void{
    const body:HTMLElement = document.body
    const image:HTMLImageElement = document.createElement('img');
    const imageContainer:HTMLDivElement = document.createElement('div');
    const btn:HTMLButtonElement = document.createElement('button');

    body.classList.add('showingImage')
    
    btn.textContent = 'X'
    
    image.src = `build/img/grande/${id}.jpg`
    
    imageContainer.className = 'imageContainer'
    imageContainer.onclick = () => {
        body.classList.remove('showingImage')
        body.removeChild(imageContainer)
        console.log('chicl');
        
    }
    imageContainer.appendChild(image)
    imageContainer.appendChild(btn)
    
    body.appendChild(imageContainer)    
}
function scrollnav():void {
    const links:NodeListOf<HTMLAnchorElement> = document.querySelectorAll('.header_nav a');
    
    links.forEach((link:HTMLAnchorElement):void => {
        
        link.addEventListener('click', (e:Event) => {
            
            e.preventDefault()
            
            const link = (e.target as HTMLAnchorElement) ?? { href: '' };
            const sectionRef = link.href.split('html')[1]
            const section = document.querySelector<HTMLElement>(sectionRef)
            
            section?.scrollIntoView({behavior:"smooth"})
        
        })
    
    })

}
function fixedNavbar():void {
    const navBar = document.querySelector<HTMLElement>('.header');
    const aboutSection = document.querySelector<HTMLElement>('.about');

    window.addEventListener('scroll', () => {
        
        const scrollReference:number = aboutSection!.getBoundingClientRect().top;
        
        if (scrollReference < 0) {
            navBar?.classList.add('barSticky') 
            console.log('fixed');
            
        } else {
            navBar?.classList.remove('barSticky') 
        }
        
    })
}